//Navigation carousel
.owl-styles {
  .owl-stage-outer {
    overflow: hidden;
  }

  .owl-nav {
    position: absolute;
    width: 100%;
    top: calc((100% - 50px) / 2);

    span {
      display: none;
    }

    button {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: none;
      background: $blue3;
      color: $white;
      font-size: 16px;
      cursor: pointer;
    }

    .owl-prev {
      position: absolute;
      left: -140px;

      &:before {
        content: '\f2ff';
        font-family: 'Material-Design-Iconic-Font';
      }
    }

    .owl-next {
      position: absolute;
      right: -140px;

      &:before {
        content: '\f301';
        font-family: 'Material-Design-Iconic-Font';
      }
    }
  }
}
@media #{$xll} {
  .owl-styles {
    .owl-nav {
      .owl-prev {
        left: -90px;
      }

      .owl-next {
        right: -90px;
      }
    }
  }
}
@media #{$xl} {
  .owl-styles {
    .owl-nav {
      .owl-prev {
        left: 0;
      }

      .owl-next {
        right: 0;
      }
    }
  }
  @media #{$sm} {

    .owl-nav {
      .owl-prev {
        left: 10px;
      }

      .owl-next {
        right: 10px;
      }
    }
  }
}
//End navigation carousel

//Choose language
.choose-language {
  position: relative;
  padding: 0 15px;
  display: flex;
  right: 10px;
  align-items: center;
  cursor: pointer;

  &:hover {
    .zmdi-chevron-down {
      color: $blue3;
    }
  }

  &.open {
    .zmdi-chevron-down {
      transform: rotate(180deg);
    }
  }

  .zmdi-chevron-down {
    transition: all .2s;
  }

  .language {
    display: flex;
    align-items: center;
    padding-right: 15px;

    img {
      width: 30px;
      height: 30px;
      margin-right: 6px;
    }
  }

  .dropdown-language {
    display: none;
    position: absolute;
    top: 70px;
    right: 0;
    box-shadow: 0 3px 12px rgba(0, 0, 0, .1);
    background: $white;
    padding: 15px;
    width: 128px;
    z-index: 9;

    &:before {
      content: '';
      position: absolute;
      top: -9px;
      left: calc((100% - 18px) / 2);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 9px 9px 9px;
      border-color: transparent transparent #ffffff transparent;

    }

    .language {
      padding-bottom: 5px;
      position: relative;
      color: $blue;

      &:last-child {
        padding-bottom: 0;
      }

      &.soon:before {
        content: 'SOON';
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        right: 0;
        font-size: 12px;
        color: #ff6f79;
        width: 100%;
        height: 30px;
        background: rgba(255, 255, 255, .4);
      }
    }
  }
}

//End choose language

//STYLES SWITCHER
.styles-switcher {
  position: relative;
  min-width: 56px;
  height: 26px;
  -webkit-appearance: none;
  background: transparent;
  outline: none;
  border-radius: 20px;
  border: 1px solid #d1dee9;
  transition: .5s;

  &:checked {
    background: $blue3;

    &:before {
      left: 30px;
      background: #fff;
    }
  }

  &:before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    top: 2.3px;
    left: 4px;
    background: #7f87a5;
    transition: .5s;
  }
}

//END STYLES SWITCHER

//STYLES CHECKBOX
.check {
  margin-top: 20px;
  padding-left: 20px;

  label {
    position: relative;
  }

  input[type=checkbox] + label:before {
    content: "\2713";
    color: transparent;
    display: inline-block;
    border: 1px solid #e1eef6;
    font-size: 14px;
    line-height: 13px;
    margin: -5px 5px 0 0;
    background: #f9fcfe;
    height: 14px;
    width: 14px;
    text-align: center;
    vertical-align: middle;
    transition: color ease .3s;
    position: absolute;
    left: -20px;
    top: 6px;
  }

  input[type=checkbox]:checked + label:before {
    color: #4e5983;
  }
}

//END STYLES CHECKBOX

.styles-btn-circle {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  border-radius: 20px;
  border: 1px solid #cae4fd;
  font-size: 15px;
  color: $blue;
  padding: 0 33px 0 5px;
  transition: all .3s ease-in-out;

  &:hover {
    background: $blue3;
    color: $white;

    span {
      background: $white;

      .zmdi {
        color: $blue3;
      }
    }
  }

  span {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $blue3;
    border-radius: 50%;
    margin-right: 20px;
    transition: all .3s ease-in-out;

    .zmdi {
      color: $white;
      transition: all .3s ease-in-out;
    }
  }
}